import "core-js/modules/es.array.push.js";
import AdminToggleDark from './AdminToggleDark.vue';
export default {
  name: 'DashboardTabs',
  components: {
    AdminToggleDark
  },
  props: {
    activeTab: {
      type: String,
      default: 'dashboard'
    }
  },
  computed: {
    titleName() {
      const titles = {
        dashboard: 'Gallery',
        customerConfig: '用户管理',
        systemConfig: '系统设置',
        default: '上传页面'
      };
      return titles[this.activeTab] || titles.default;
    },
    iconName() {
      const icons = {
        dashboard: 'images',
        customerConfig: 'user-cog',
        systemConfig: 'cogs',
        default: 'upload'
      };
      return icons[this.activeTab] || icons.default;
    }
  },
  methods: {
    refreshDashboard() {
      location.reload();
    },
    handleTabClick(tab) {
      this.$router.push(`/${tab}`);
    },
    handleDropdownVisible(isVisible) {
      const dropdownLink = document.querySelector('.tabs-dropdown-link');
      if (isVisible) {
        dropdownLink.classList.add('active');
      } else {
        dropdownLink.classList.remove('active');
      }
    }
  }
};