import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
import DashboardTabs from '@/components/DashboardTabs.vue';
export default {
  name: 'CustomerConfig',
  data() {
    return {
      tableData: [],
      dealedData: [],
      // 根据IP地址处理后的数据，格式为 {ip, count, [data]}
      blockipList: [],
      // 禁止上传的IP列表
      loading: false,
      // 分页数据
      currentPage: 1,
      pageSize: 10 // 默认每页10条
    };
  },
  components: {
    DashboardTabs
  },
  computed: {
    ...mapGetters(['credentials']),
    disableTooltip() {
      return window.innerWidth < 768;
    },
    paginatedData() {
      // 计算分页数据
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.dealedData.slice(start, end);
    }
  },
  methods: {
    getFileIcon(fileType) {
      if (!fileType) return 'file';
      if (fileType.includes('image')) return 'file-image';
      if (fileType.includes('video')) return 'file-video';
      if (fileType.includes('audio')) return 'file-audio';
      if (fileType.includes('pdf')) return 'file-pdf';
      if (fileType.includes('word') || fileType.includes('document')) return 'file-word';
      if (fileType.includes('excel') || fileType.includes('spreadsheet')) return 'file-excel';
      if (fileType.includes('powerpoint') || fileType.includes('presentation')) return 'file-powerpoint';
      if (fileType.includes('zip') || fileType.includes('archive')) return 'file-archive';
      if (fileType.includes('code') || fileType.includes('text')) return 'file-code';
      return 'file';
    },
    tableRowClassName({
      row
    }) {
      return row.enable ? '' : 'blocked-row';
    },
    async fetchWithAuth(url, options = {}) {
      if (this.credentials) {
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    formatTimeStamp(timeStamp) {
      const date = new Date(timeStamp);
      return new Intl.DateTimeFormat('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      }).format(date);
    },
    sortByTimestamp(a, b) {
      return new Date(a.metadata.TimeStamp) - new Date(b.metadata.TimeStamp);
    },
    async handleSwitchEnable(row) {
      const ip = row.ip;
      const enable = row.enable;
      try {
        if (enable) {
          // 从 blockipList 中移除
          this.blockipList = this.blockipList.filter(item => item !== ip);
          // 更新 blockipList
          await this.fetchWithAuth("/api/manage/cusConfig/whiteip", {
            method: 'POST',
            body: ip
          });
          this.$message({
            message: `已允许 ${ip} 上传文件`,
            type: 'success',
            icon: '<i class="fas fa-check-circle"></i>'
          });
        } else {
          // 添加到 blockipList 中
          this.blockipList.push(ip);
          // 更新 blockipList
          await this.fetchWithAuth("/api/manage/cusConfig/blockip", {
            method: 'POST',
            body: ip
          });
          this.$message({
            message: `已禁止 ${ip} 上传文件`,
            type: 'warning',
            icon: '<i class="fas fa-ban"></i>'
          });
        }
      } catch (error) {
        this.$message.error('操作失败，请重试');
        // 恢复原状态
        row.enable = !enable;
      }
    },
    handlePageChange(page) {
      this.currentPage = page;
      // 到最后一页时，加载更多数据
      if (page === Math.ceil(this.dealedData.length / this.pageSize)) {
        this.loadMoreData();
      }
    },
    async loadMoreData() {
      this.loading = true;
      try {
        const start = this.dealedData.length;
        const count = 20; // 每次加载20条数据
        const response = await this.fetchWithAuth(`/api/manage/cusConfig/list?start=${start}&count=${count}`, {
          method: 'GET'
        });
        const result = await response.json();
        if (result.length === 0) {
          this.$message({
            message: '已加载全部数据',
            type: 'info',
            icon: '<i class="fas fa-info-circle"></i>'
          });
          return;
        }
        this.dealedData = this.dealedData.concat(result.map(item => {
          const enable = !this.blockipList.includes(item.ip);
          return {
            ip: item.ip,
            count: item.count,
            data: item.data,
            enable: enable
          };
        }));
        this.$message({
          message: `成功加载 ${result.length} 条新数据`,
          type: 'success',
          icon: '<i class="fas fa-check-circle"></i>'
        });
      } catch (error) {
        this.$message.error('加载更多数据时出错，请检查网络连接');
      } finally {
        this.loading = false;
      }
    },
    handleSizeChange(size) {
      this.pageSize = size;
      this.currentPage = 1;
    }
  },
  async mounted() {
    this.loading = true;
    try {
      const checkResponse = await this.fetchWithAuth("/api/manage/check", {
        method: 'GET'
      });
      const checkResult = await checkResponse.text();
      if (checkResult == "true" || checkResult == "Not using basic auth.") {
        // 在 check 成功后再执行 list 的 fetch 请求
        const listResponse = await this.fetchWithAuth("/api/manage/cusConfig/list?count=20", {
          method: 'GET'
        });
        const listResult = await listResponse.json();

        // 读取blockipList, 接口返回格式为 'ip1,ip2,ip3'，需要转换为数组
        const blockipList = await this.fetchWithAuth("/api/manage/cusConfig/blockipList", {
          method: 'GET'
        });
        this.blockipList = (await blockipList.text()).split(',');
        this.dealedData = listResult.map(item => {
          const enable = !this.blockipList.includes(item.ip);
          return {
            ip: item.ip,
            count: item.count,
            data: item.data,
            enable: enable
          };
        });
      } else {
        throw new Error('Unauthorized');
      }
    } catch (err) {
      if (err.message !== 'Unauthorized') {
        this.$message.error('同步数据时出错，请检查网络连接');
      }
    } finally {
      this.loading = false;
    }
  }
};